<template>
    <div>
        <TopNav title="库存价格"></TopNav>
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <el-form  :rules="rules" :model="goodsDetail" ref="goodsprice" size="mini"
                         label-width="100px">
                    <div class="wyu-tips wyu-tips-warning">
                        <p> 1、 开启会员优惠 则必须填写会员优惠价格，则表示没有会员优惠</p>
                        <p> 2、 商品属性 填写则展示 不填写则不展示 ，商品模型 与 是否有规格 不可修改 请认证填写信息</p>
                        <p> 3、 开启分佣 则必须填写 分佣价格，关闭分佣 则视为 不参与分佣商品</p>
                        <p> 4、 规格与商品模型 不可修改</p>
                    </div>
                    <div class="wyu-title">
                        <h2>价格&属性</h2>
                    </div>
                    <el-form-item label="商品模型">
                        <select-type  :disabled="true" v-model="goodsDetail.type_id" :type-id="goodsDetail.type_id" style="width: 300px"
                                     size="mini"></select-type>
                    </el-form-item>
                    <el-form-item label="会员专享">
                        <el-radio-group size="mini" v-model="goodsDetail.is_plus_vip">
                            <el-radio :label="0">会员无专享</el-radio>
                            <el-radio :label="1">会员有专享</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="规格">
                        <el-radio-group :disabled="true" size="mini" v-model="goodsDetail.sku_type">
                            <el-radio :label="0">无规格</el-radio>
                            <el-radio :label="1">多规格</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="参与分佣">
                        <el-radio-group size="mini" v-model="goodsDetail.is_commission">
                            <el-radio :label="0">关闭分佣</el-radio>
                            <el-radio :label="1">开启分佣</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="是否可用优惠券">
                        <el-radio-group size="mini" v-model="goodsDetail.is_coupon">
                            <el-radio :label="0">不可使用</el-radio>
                            <el-radio :label="1">可以使用</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <div class="wyu-title">
                        <h2>商品属性</h2>
                    </div>
                    <el-form-item label-width="10px">
                        <goods-attr :attr-data="goodsDetail.goods_attr" v-model="goodsDetail.goods_attr"
                                    :type-id="goodsDetail.type_id"></goods-attr>
                    </el-form-item>
                    <div class="wyu-title">
                        <h2>商品价格 <span style="font-size: 10px">（会员价格设置0员则表示没有会员优惠）</span></h2>
                    </div>
                    <el-form-item label-width="10px" v-if="goodsDetail.sku_type == 1" prop="checkGoodsPrice">
                        <goods-spec
                                :spec-data="goodsDetail.goods_spec"
                                :is-plus-vip="goodsDetail.is_plus_vip"
                                :is-commission="goodsDetail.is_commission"
                                v-model="goodsDetail.goods_spec"
                                :type-id="goodsDetail.type_id">
                        </goods-spec>
                    </el-form-item>
                    <div v-if="goodsDetail.sku_type == 0">
                        <el-form-item label="市场价">
                            <el-input style="width: 500px" v-model="goodsDetail.market_price"></el-input>
                        </el-form-item>
                        <el-form-item label="本店价">
                            <el-input style="width: 500px" v-model="goodsDetail.shop_price"></el-input>
                        </el-form-item>
                        <el-form-item label="会员价" v-if="goodsDetail.is_plus_vip == 1">
                            <el-input style="width: 500px" v-model="goodsDetail.plus_vip_price"></el-input>
                        </el-form-item>
                        <el-form-item label="库存">
                            <el-input style="width: 500px" v-model="goodsDetail.goods_num"></el-input>
                        </el-form-item>
                        <el-form-item label="佣金" v-if="goodsDetail.is_commission == 1" >
                            <el-input v-model="goodsDetail.commission"   style="width: 500px"></el-input>
                        </el-form-item>
                    </div>
                </el-form>
            </div>
        </div>
        <div class="wyu-btn-div">
            <el-button size="small" type="primary"    @click="createGoods()" >提交修改</el-button>
            <router-link to="/product/product/index"  style="margin-left: 10px"><el-button size="small">取消</el-button></router-link>
        </div>
    </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";
    import SelectType from "../type/components/SelectType";
    import GoodsSpec from "../components/GoodsSpec";
    import GoodsAttr from "../components/GoodsAttr";
    export default {
        name: 'GoodsCreate',
        components: {
             GoodsAttr,GoodsSpec,SelectType,TopNav
        },
        data() {
            return {
                loading:false,
                active: 1,
                typeId: 0,
                formNameKey: ['goodsInfo', 'goodsprice', 'goodsArea'],
                goodsDetail: {
                    cat_id: 0,//分类ID
                    p_cat_id: 0,//为可视化部分冗余的 父级分类ID
                    goods_sn: '',//商品编号
                    goods_name: "", //商品名称
                    brand_id: 0,//品牌ID
                    store_count: 0,//库存数量
                    comment_count: 0,//商品评论数量
                    weight: 0,//商品重量 单位克
                    market_price: 0,//市场价
                    shop_price: 0,//本店价
                    keywords: '',//商品关键词
                    goods_content: [],//商品详情
                    photo: '',//商品缩略图
                    photos: [],//商品相册
                    is_online: 1, //是否上架
                    is_free_shipping: 1,//是否包邮
                    sort: 0,//商品排序
                    is_recommend: 0,//是否推荐
                    is_new: 0,//是否是新品
                    is_hot: 0,//是否是热卖
                    give_integral: 0,//购买商品赠送积分
                    sales_sum: 0,//商品销量
                    commission: 0,//分佣提成
                    shipping_area_ids: [],//配送物流
                    plus_vip_price: 0,//会员专享价格
                    goods_attr: [],//商品属性
                    goods_spec: {},//是否有佣金
                    sku_type: 1,//商品模型
                    type_id: 0,//商品模型
                    is_plus_vip: 0,//是否有会员优惠
                    is_commission: 0,//是否有佣金
                    is_coupon: 0,//是否可以优惠券
                },
                //校验规则
                rules: {
                    goods_name: [
                        {required: true, message: '请输入商品名称', trigger: 'blur'},
                        {min: 1, max: 64, message: '长度在 1 到 64 个字符', trigger: 'blur'}
                    ],
                    goods_sn: [
                        {required: true, message: '请输入商品编号', trigger: 'blur'},
                        {min: 1, max: 64, message: '长度在 1 到 64 个字符', trigger: 'blur'}
                    ],

                    photo: [
                        {required: true, message: '请上传缩略图', trigger: 'blur',}
                    ],
                    photos: [
                        {type: 'array', required: true, message: '至少上传一张相册', trigger: 'blur'}
                    ],
                    goods_content: [
                        {type: 'array', required: true, message: '请编辑详情', trigger: 'blur'}
                    ],
                }
            }
        },

        created() {
                this.getSpecPrice();
        },
        methods: {
            //获取价格信息
            getSpecPrice(){
                this.loading = true;
                api.post("/shop/admin.goods.goods/getSpecDetail", {
                    appToken: this.$store.getters.appToken,
                    goods_id:this.$route.params.id,
                }).then(res => {
                    this.goodsDetail = res.detail;
                    this.loading = false;
                }).catch(() => {
                    this.loading = false;
                });
            },
            //验证商品模型
            checkGoodsType() {
                return  this.goodsDetail.type_id > 0;
            },
            //验证商品价格
            checkGoodsPrice() {
                if (this.goodsDetail.sku_type == 1) {
                    let returnBol = true;
                    this.goodsDetail.goods_spec.itemPrice.some((val) => {
                        if (val.invalid == 0) {
                            if (val.market_price <= 0 || val.store_count <= 0 || val.weight <= 0) {
                                returnBol = false;
                                return false;
                            }
                            if (this.goodsDetail.is_plus_vip == 1 && val.plus_vip_price <= 0) {
                                returnBol = false;
                                return false;
                            }
                            if (this.goodsDetail.is_commission == 1 && val.commission <= 0) {
                                returnBol = false;
                                return false;
                            }
                        }
                    })
                    return returnBol;
                } else {
                    if (this.goodsDetail.market_price <= 0 || this.goodsDetail.store_count <= 0 || this.goodsDetail.weight <= 0) {
                        return false;
                    }
                    if (this.goodsDetail.is_plus_vip == 1 && this.goodsDetail.plus_vip_price <= 0) {
                        return false;
                    }
                    if (this.goodsDetail.is_commission == 1 && this.goodsDetail.commission <= 0) {
                        return false;
                    }
                }
                return true;
            },


            //提交商品到商品库
            createGoods() {
                this.loading  = true;
                api.post("/shop/admin.goods.goods/setSpecDetail", {
                    appToken: this.$store.getters.appToken,
                    goods_id:this.$route.params.id,
                    store_count: this.goodsDetail.store_count,  //库存数量
                    weight: this.goodsDetail.weight,  //商品重量 单位克
                    market_price: this.goodsDetail.market_price,  //市场价
                    shop_price: this.goodsDetail.shop_price,  //本店价
                    commission: this.goodsDetail.commission,  //分佣提成
                    plus_vip_price: this.goodsDetail.plus_vip_price,  //会员专享价格
                    goods_attr: JSON.stringify(this.goodsDetail.goods_attr),  //商品属性
                    goods_spec: JSON.stringify(this.goodsDetail.goods_spec),  //是否有佣金
                    is_plus_vip: this.goodsDetail.is_plus_vip,  //是否有会员优惠
                    is_commission: this.goodsDetail.is_commission,  //是否有佣金
                    is_coupon: this.goodsDetail.is_coupon,  //是否有佣金
                    goods_num: this.goodsDetail.goods_num,  //是否有佣金
                }).then(res => {
                    this.loading = false;
                    this.$message({
                        message: '提交成功',
                        type: 'success'
                    });
                }).catch((error) => {
                    this.loading = false;
                });
            },
        }
    }
</script>
